/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
    box-sizing: border-box;
  }
  
  /* 2. Remove default margin */
  *:not(dialog) {
    margin: 0;
  }
  
  /* 3. Enable keyword animations */
  @media (prefers-reduced-motion: no-preference) {
    html {
      interpolate-size: allow-keywords;
    }
  }
  
  body {
    /* 4. Add accessible line-height */
    line-height: 1.5;
    /* 5. Improve text rendering */
    -webkit-font-smoothing: antialiased;
  }
  
  /* 6. Improve media defaults */
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  
  /* 7. Inherit fonts for form controls */
  input, button, textarea, select {
    font: inherit;
  }
  
  /* 8. Avoid text overflows */
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  
  /* 9. Improve line wrapping */
  p {
    text-wrap: pretty;
  }
  h1, h2, h3, h4, h5, h6 {
    text-wrap: balance;
  }
  
  /*
    10. Create a root stacking context
  */
  #root, #__next {
    isolation: isolate;
  }


/* Layout styling */

body {
  background-color: #696969;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color:#F8F8FF;
  height:100vh;
  display: flex;
  flex-direction: column;
}

header {
  border-bottom: 2px solid #F8F8FF;
  flex:0 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

header h1 {
  font-size: 3rem;
  padding-left:30px;
}

ul {
  list-style-type: none;
  padding:0;
}

.projectBtn {
  background-color: transparent;
  font-size: 2rem;
  text-align: start;
  color:#F8F8FF;
  border: none;
}

div#ui {
    display:flex;
    flex-direction: row;
    flex:1 0 auto;
}

nav > section {
  padding:20px 0px 20px 30px;
}

div#ui > nav {
    border-right:2px solid #F8F8FF;
    display:flex;
    flex-direction: column;
    height:100%;
    width:250px;
}

nav > section > h2 {
  font-size: 2.5rem; 
}



button#addProject, button#addTodo, button.deleteProject {
  background-color: transparent;
  font-size: 1.3rem;
  color:#F8F8FF;
  border:1px solid #F8F8FF;
  border-radius: 5px;
  padding:5px 15px;
  width:max-content;
  font-weight: 700;
}

button#addTodo {
  margin-right:40px;
}

button#addProject:hover, button#addTodo:hover, button.deleteProject:hover {
  color:black;
  background-color:#F8F8FF;
  cursor: pointer;

}

form button:hover {
  cursor: pointer;
}

nav button:hover {
  cursor: pointer;
  color:bisque;
}

/* Todo itembox styling */
div#ui > div#todos {
  display: flex;
  flex-direction: column;
  flex:20 1 auto;
  align-items: center;
  padding-top:40px;
  gap:30px;
}

.itemBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 1px solid black;
    background-color:#F8F8FF;
    border-radius: 5px;
    color:black;
    width:70%;
    padding:1rem;
    flex-wrap: wrap;
    gap:40px;
}

.itemBox > input[type="checkbox"] {
    transform:scale(2);
    
}

.itemBox > h3 {
    padding-left:50px;
    min-width: 40%;
}

.itemBox > p {
    min-width:20%;
}

.itemBox > button.todoDetails {
    height:30px;
    width:70px;
    background-color: transparent;
    border-radius: 5px;
}

.itemBox > button.todoDetails:hover {
  background-color: bisque;
  
}

.itemBox > button.deleteTodo {
  min-height:30px;
  min-width:30px;
  border:none;
  background-color: transparent;
}

.itemBox > button:hover {
  cursor: pointer;
  
}

.deleteTodo {
    background-image:url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
}

div#todos > h2 {
  text-align: center;
  font-size: 2rem;
  border-bottom: 1px solid #F8F8FF;
}

div#todos > p {
  font-size: 1.5rem;
